.dropdownContent {
  padding: 8px;
  width: 320px;
  color: rgba(0,0,0);
  .gridContainer {
    display: grid;
    gap: 8px;
    &.controlWrap {
      height: 148px;
      grid-template-columns: repeat(4, minmax(0, 1fr));
      grid-template-rows: repeat(2, minmax(0, 1fr));
    }
    &.historyWrap {
      height: 226px; // 70*3+8*2
      grid-template-columns: repeat(4, minmax(0, 1fr));
      grid-template-rows: repeat(3, minmax(0, 1fr));
      margin-top: 16px;
    }
    .wrap {
      border-radius: 3px;
      color: var(--system-theme-color);
      background-color: var(--system-theme-bgcolor-hover);
      box-shadow: 0 1px 5px #0000004d;
      padding: 8px;
      transition: all 0.3s;
      &:hover {
        transform: translateY(-1px);
      }

      &.row-span-2 {
        grid-row: span 2/span 2;
      }

      &.col-span-2 {
        grid-column: span 2/span 2;
      }
    }
  }
}

.warehouse {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  .item {
    display: flex;
    align-items: center;
    cursor: pointer;
    position: relative;
    justify-content: space-between;
    &:hover {
      .arrow {
        opacity: 0.45;
      }
    }
    .info {
      display: flex;
      align-items: center;
    }
    .icon {
      width: 32px;
      height: 32px;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-shrink: 0;
      border-radius: 50%;
      margin-right: 8px;
      font-size: 32px;
      background-color: #fff;
      overflow: hidden;
    }
    .title {
      font-size: 14px;
      line-height: 16px;
      font-weight: 500;
    }
    .desc {
      font-size: 12px;
      line-height: 14px;
      opacity: 0.45;
    }
    .arrow {
      font-size: 12px;
      opacity: 0;
    }
  }
}

.theme {
  display: flex;
  align-items: center;
  cursor: pointer;
  .icon {
    width: 32px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    border-radius: 50%;
    margin-right: 8px;
    font-size: 18px;
    overflow: hidden;
    background-color: #9ca3af40;
    color: rgb(55, 65, 81);
    &.dark {
      background-color: rgb(59, 130, 246);
      color: #fff;
    }
  }
  .title {
    font-weight: 500;
  }
}

.fullscreen, .idcard, .lifestyle {
  text-align: center;
  cursor: default;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  cursor: pointer;
  .icon {
    font-size: 16px;
    line-height: 16px;
    height: 16px;
    display: flex;
    align-items: center;
  }
  .title {
    font-size: 12px;
    line-height: 14px;
    margin-top: 6px;
  }
}

.idcard {
  .icon {
    font-size: 20px;
  }
}
