.batteryButton {
  position: relative;
  display: flex;
  align-items: center;
  & > svg {
    font-size: 18px;
    margin: -1px;
  }
  .batteryLevel {
    position: absolute;
    top: 50%;
    left: 1px;
    transform: translateY(-50%);
    height: 5px;
    border-radius: 0.5px;
  }
  .batteryCharging {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -6px;
    font-size: 10px;
    margin-top: -5px;
  }
}
