.dropdownContent {
  padding: 8px;
  width: 268px;
  .title {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 8px;
    margin-bottom: 8px;
    border-bottom: 1px dashed var(--system-theme-color);
    h1 {
      margin-bottom: 0;
      font-size: 14px;
      color: var(--system-theme-color);
      opacity: 0.85;
    }
    span {
      font-size: 14px;
      color: var(--system-theme-color);
      opacity: 0.65;
    }
  }
  .desc {
    font-size: 12px;
    color: var(--system-theme-color);
    line-height: 20px;
    opacity: 0.65;
  }
}
