.taskbarButton {
  cursor: pointer;
  padding: 0 4px;
  margin: 0 4px;
  border-radius: 3px;
  height: 24px;
  display: inline-flex;
  align-items: center;
  transition: all .1s ease-out;
  &:hover {
    color: var(--system-theme-color-hover);
    background-color: var(--system-theme-bgcolor-hover);
  }
}

.dropdownRender {
  ._dropdownMenuRenderTheme();
}

:global {
  body > div > .eleven-os-dropdown-menu-dropdownMenuPopup {
    &.eleven-os-dropdown-menu-submenu {
      ._dropdownMenuRenderTheme();
    }
  }
}

._dropdownMenuRenderTheme() {
  color: var(--system-theme-color);
  background-color: var(--system-theme-bgcolor);
  box-shadow: rgba(26, 26, 26, 0.5) 0px 3px 10px 3px;
  border-radius: 3px;
  overflow: hidden;
  :global {
    .eleven-os-dropdown-menu {
      padding: 0;
      background: transparent;
      border-radius: inherit;
    }

    ._dropdownMenuTheme();
  }
}

._dropdownMenuTheme() {
  .eleven-os-dropdown-menu .eleven-os-dropdown-menu-item {
    color: var(--system-theme-color);
    opacity: 0.85;
    border-radius: 0px;

    &:hover {
      opacity: 0.95;
      color: var(--system-theme-color-hover);
      background-color: var(--system-theme-bgcolor-hover);
    }
  }

  .eleven-os-dropdown-menu .eleven-os-dropdown-menu-item-group-title {
    color: var(--system-theme-color);
    opacity: 0.45;
  }

  .eleven-os-dropdown-menu .eleven-os-dropdown-menu-submenu-title {
    color: var(--system-theme-color);
    opacity: 0.85;
    padding: 5px 12px;
    border-radius: 0;
    line-height: 22px;
    display: flex;
    align-items: center;
  }

  .eleven-os-dropdown-menu .eleven-os-dropdown-menu-submenu-title .eleven-os-dropdown-menu-submenu-expand-icon .eleven-os-dropdown-menu-submenu-arrow-icon {
    color: currentColor;
    opacity: 0.65;
  }

  .eleven-os-dropdown-menu .eleven-os-dropdown-menu-item-group-list {
    margin: 0;
  }

  .eleven-os-dropdown-menu .eleven-os-dropdown-menu-item-divider {
    background-color: var(--system-theme-color);
    opacity: 0.15;
  }
}
